*{
  scroll-behavior: smooth;
}

body{
  background-color: #1D1A26;
}

body::-webkit-scrollbar{
  width: 7px;
}

body::-webkit-scrollbar-thumb{
  width: 0;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  background-color: #B40803;
}


.header{
  width: 100%;
  height: 100vh;
  position: relative;
  top: 0;
}

.nav{
  height: 13vh;
  width: 100%;
  position: absolute;
  display: flex;
  z-index: 400;
  top: 0;
  justify-content: space-between;
}

.header-logo{
  padding: 5px;
  object-fit: contain;
  object-position: 15%;
  width: 45%;
}

.button-container{
  display: flex;
  justify-content: space-around;
  width: 54%;
}

.header-button{
  background-color: transparent;
  border: none;
  font-size: 1.1rem;
  font-weight: 700;
  color: #FFF;
  transition: color .3s ease-in-out;
  -webkit-transition: color .3s ease-in-out;
  -moz-transition: color .3s ease-in-out;
  -ms-transition: color .3s ease-in-out;
  -o-transition: color .3s ease-in-out;
  cursor: pointer;
}

.header-button:hover{
  color: #BBB;
}

.title-content{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 300;
  background-image: linear-gradient(to top, #ff084477 0%, #ffb19966 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;  
}

.video-header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  object-fit: cover;
  object-position: center;
  z-index: 200;
}

.title-content{
  padding: 0 10%;
}

.header-title{
  font-size: 7rem;
  text-align: center;
  color: #FFF;
  font-family: 'Noto Serif JP', serif;
} 

.header-subtitle{
  font-size: 1.1rem;
  text-align: center;
  color: #FFF;
  font-family: 'Noto Serif JP', serif;
}

.title-button{
  background-color: #555;
  padding: .7rem 1.5rem;
  font-weight: 700;
  font-size: 1.3rem;
  color: #FFF;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
}

@media screen and (max-width: 700px){
  .header{
    height: calc(1vh * 100);
    height: calc(var(--vh,1vh) * 100);
  }
  .nav{
    display: none;
  }
  .header-title{
    font-size: 6rem;
  } 
  
  .header-subtitle{
    font-size: 1rem;
  }
}

@media screen and (max-width: 500px){
  .header-title{
    font-size: 5rem;
  } 
  
  .header-subtitle{
    font-size: .8rem;
  }
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}

p{
  color: #FFF;
}

.block{
  width: 90%;
  max-width: 1400px;
  padding: 5vh 0;
  margin: auto;
}

.main-container{
  display: grid;
  grid-auto-rows: 250px;
  grid-template-columns: repeat(6,1fr);
  grid-gap: 3rem .7rem;
  gap: 3rem .7rem;
}

.card{
  position: relative;
  border: none;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
  overflow-y: hidden;
}

.card-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-header,.card-footer{
  position: absolute;
  width: 100%;
}

.card-header{
  background: rgb(71,34,34);
  background: linear-gradient(180deg, #111E,#222A,#3330);
  padding: 10px;
  transform: translateY(-101%);
  -webkit-transform: translateY(-101%);
  -moz-transform: translateY(-101%);
  -ms-transform: translateY(-101%);
  -o-transform: translateY(-101%);
  transition: transform .2s ease-in-out;
  -webkit-transition: transform .2s ease-in-out;
  -moz-transition: transform .2s ease-in-out;
  -ms-transition: transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
}

.card-header-title{
  color: #EEEEEE;
}

.card-footer{
  height: 25%;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(101%);
  -webkit-transform: translateY(101%);
  -moz-transform: translateY(101%);
  -ms-transform: translateY(101%);
  -o-transform: translateY(101%);
  transition: transform .2s ease-in-out;
  -webkit-transition: transform .2s ease-in-out;
  -moz-transition: transform .2s ease-in-out;
  -ms-transition: transform .2s ease-in-out;
  -o-transition: transform .2s ease-in-out;
  background: linear-gradient(0deg, #222C,#4449,#5550);
}

.card-footer-button{
  color: #FFF;
  background-color: #B40803;
  padding: 8px 12px;
  border-radius: 5px;
  border: none;
  text-decoration: none;
  font-weight: 700;
  cursor: pointer;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.card:hover .card-header{
  transform: translateY(0%);
  -webkit-transform: translateY(0%);
  -moz-transform: translateY(0%);
  -ms-transform: translateY(0%);
  -o-transform: translateY(0%);
}

.card:hover .card-footer{
  transform: translateY(0);
  -webkit-transform: translateY(0);
  -moz-transform: translateY(0);
  -ms-transform: translateY(0);
  -o-transform: translateY(0);
}

.error{
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: center;
  padding: 10vh 5vw;
}

.error-img{
  width: 25%;
  object-fit: contain;
}

.error-text{
  color: #EEE;
}

.loading{
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: #1D1A26;
  z-index: 90000;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.loading-text{
  font-size: 2rem;
  color: #EEE;
}

.loading-icon{
  font-size: 6rem;
  font-weight: 700;
  animation-name: colorLetter;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.point{
  animation-name: points;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  font-size: 2.5rem;
}

.point:first-child{
  animation-delay: 1s;
}

.point:nth-child(2){
  animation-delay: 2s;
}

.point:last-child{
  animation-delay: 3s;
}

@media screen and (max-width: 1200px){
  .main-container{
    grid-template-columns: repeat(5,1fr);
  }
}

@media screen and (max-width: 900px){
  .main-container{
    grid-template-columns: repeat(4,1fr);
  }
}

@media screen and (max-width: 700px){
  .block{
    width: 90%;
  }
  .main-container{
    grid-template-columns: repeat(3,1fr);
  }
  .loading-icon{
    font-size: 5rem;
  }
}

@media screen and (max-width: 500px){
  .main-container{
    grid-template-columns: repeat(2,1fr);
  }
  .loading-icon{
    font-size: 4rem;
  }
}

@keyframes colorLetter{
  0%{
    color: #B40803;
  }

  50%{
    color: #1D1A26;
  }

  100%{
    color: #FFF;
  }
}

@keyframes points{
  0%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}
.form-fieldset{
  border: none;
}

.form-block{
  margin-top: 5vh;
  display: flex;
  flex-direction: column;
}

.searchbar-fieldset{
  display: flex;
  border: none;
  margin-bottom: 2vh;
}

.search-bar{
  flex-grow: 1;
  height: 8vh;
  border: none;
  border-radius: 10px 0 0 10px;
  -webkit-border-radius: 10px 0 0 10px;
  -moz-border-radius: 10px 0 0 10px;
  -ms-border-radius: 10px 0 0 10px;
  -o-border-radius: 10px 0 0 10px;
  padding: 10px;
  font-size: 130%;
  color: #BABABA;
  background-color: #2E2B37;
}

.search-bar:focus{
  outline: none;
}

.search-bar-section{
  flex-grow: 1;
  display: flex;
  position: relative;
}

.clean-button-cont,.searchBar-search-button{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  color: #FFF;
  font-size: 2rem;
}

.searchBar-search-button{
  margin-left: 1rem;
}
.clean-button-cont{
  padding: 0 5px;
  margin-left: auto;
  background-color:#2E2B37;
  border-radius: 0 10px 10px 0;
  -webkit-border-radius: 0 10px 10px 0;
  -moz-border-radius: 0 10px 10px 0;
  -ms-border-radius: 0 10px 10px 0;
  -o-border-radius: 0 10px 10px 0;
}

.clean-button-cont,.searchBar-search-button,.trash-icon, .chevron{
  transition: color 200ms ease-in-out;
  -webkit-transition: color 200ms ease-in-out;
  -moz-transition: color 200ms ease-in-out;
  -ms-transition: color 200ms ease-in-out;
  -o-transition: color 200ms ease-in-out;
}
.clean-button-cont:hover,.searchBar-search-button:hover,.trash-icon-cont:hover .trash-icon,.show-form-button:hover .chevron{
  color: #B40803;
}

.clean-button{
  animation-name: initialX;
  animation-duration: .6s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
}

.form-fieldset-input label{
  color: #BABABA;
  font-weight: 500;
}

.filter-fieldset{
  background-color: #2E2B37;
  display: flex;
  overflow-y: hidden;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 0;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: height .3s linear;
  -webkit-transition: height .3s linear;
  -moz-transition: height .3s linear;
  -ms-transition: height .3s linear;
  -o-transition: height .3s linear;
}

.filter-fieldset-section{
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
  visibility: hidden;
  justify-content: space-evenly;
}

.form-fieldset-input{
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 7px;
  gap: 7px;
}

.form-fieldset-input select,.form-fieldset-input input{
  border: none;
  padding: 7px;
  color: #FFF;
  background-color: #2E2B37;
  border-bottom: 2px solid #B40803;
  height: 2rem;
}

.form-fieldset-input select:focus,.form-fieldset-input input:focus{
  outline: none;
}

.trash-icon-cont{
  display: flex;
  background-color: transparent;
}

.trash-button{
  align-self: center;
  border: none;
  visibility: hidden;
  background-color: transparent;
}

.trash-icon{
  color: #FFF;
  font-size: 1.5rem;
}

.form-fieldset-input{
  width: 23%;
}
.form-fieldset-input select,.form-fieldset-input input{
  width: 100%;
}

.show-form-button{
  border: none;
  background-color: transparent;
  margin-top: .5rem;
  align-self: center;
  transform: rotateZ(0);
  -webkit-transform: rotateZ(0);
  -moz-transform: rotateZ(0);
  -ms-transform: rotateZ(0);
  -o-transform: rotateZ(0);
  transition: transform .3s ease-in-out;
  -webkit-transition: transform .3s ease-in-out;
  -moz-transition: transform .3s ease-in-out;
  -ms-transition: transform .3s ease-in-out;
  -o-transition: transform .3s ease-in-out;
}

.show-form-button--spine{
  transform: rotateZ(180deg);
  -webkit-transform: rotateZ(180deg);
  -moz-transform: rotateZ(180deg);
  -ms-transform: rotateZ(180deg);
  -o-transform: rotateZ(180deg);
}

.chevron{
  color: #FFF;
  font-size: 1.5rem;
}
